  const flags = regex.flags.includes("g") ? regex.flags : `${regex.flags}g`;
  const pattern = new RegExp(regex.source, flags);
  let count = 0;
  while (pattern.exec(text)) {
    count++;
    if (count > 1000) break;
  }
  return count;
